import React from "react";
import Panel from "../Panel/Panel";
import "./Accordion.css";

// {` ${type === 'horizontal' ? '' : ''}`}

class Accordion extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      activeTab: 0,
    };

    this.activateTab = this.activateTab.bind(this);
  }

  activateTab(index) {
    this.setState((prev) => ({
      activeTab: prev.activeTab === index ? -1 : index,
    }));
  }

  render() {
    const { panels } = this.props;
    const { type }  = this.props;
    const { activeTab } = this.state;
    

    return (
      <div
        className={`accordion ${
          type === "horizontal" ? "accordion__type_horizontal" : ""
        }`}
        role="tablist"
      >
        {panels.map((panel, index) => (
          <React.Fragment key={index}>
            <Panel
              activeTab={activeTab}
              index={index}
              orientation={type}
              {...panel}
              activateTab={this.activateTab.bind(null, index)}
            />
            <div
              className={`panel__content-wrapper ${
                type === "horizontal" ? "panel__content-wrapper_horizontal" : ""
              }`}
              style={{
                opacity: `${activeTab === index ? `1` : `0`}`,
                color: `${panel.text_color}`,
                transform: `${
                  type === "horizontal"
                    ? `translateY(${activeTab * 200}px)`
                    : `translateX(${activeTab * 280}px)`
                }`,
              }}
            >
              <h2
                className={`panel__subtitle ${type === "horizontal" ? "panel__subtitle_horizontal" : ""}`}
              >
                {panel.subtitle}
              </h2>
              <h2
                className={`panel__content-title ${
                  type === "horizontal" ? "" : ""
                }`}
              >
                {panel.content_title}
              </h2>
              <p
                className={`panel__content ${type === "horizontal" ? "panel__content_horizontal" : ""}`}
              >
                {panel.content}
              </p>
            </div>
          </React.Fragment>
        ))}
      </div>
    );
  }
}

export default Accordion;
